import debounce from ".";

//æ¨¡æ‹Ÿä¸€æ®µ ajax è¯·æ±‚
const ajax = (value) => {
  // æ­¤å¤„çš„ this æŒ‡å‘ debounceInput è¿™ä¸ª input æ¡†
  console.log("ajax request " + value);
};

// æœªé˜²æŠ–
const input = document.getElementById("input");
