const throttle = (fn, delay = 100) => {
  // é€šè¿‡é—­åŒ…å­˜å‚¨ä¸€ä¸ªé”çŠ¶æ€
  let lock = false;
  return function () {
    // å¦‚æžœæ˜¯åŠ é”çŠ¶æ€ï¼Œåˆ™ç›´æŽ¥ return
    if (lock) return;
    // æ‰§è¡Œå®šæ—¶å™¨å‰å…ˆåŠ ä¸ªé”
    lock = true;
    setTimeout(() => {
      fn.apply(this, arguments);
      // å®šæ—¶å™¨æ‰§è¡Œå®ŒåŽé”æ”¹ä¸º falseï¼Œè¡¨ç¤ºå¯ä»¥æ‰§è¡Œä¸‹æ¬¡å¾ªçŽ¯äº†
      lock = false;
    }, delay);
  };
};

export default throttle;
